.btn-outlined {
    width: fit-content;
    display: flex;
    gap: 8px;
    align-items: center;
    justify-content: center;
    color: $primary-color;
    font-weight: 600;
    font-size: 18px;
    border: 2px solid $primary-color;
    height: 54px;
    padding: 0 20px;
    background-color: rgba(213,229,229,50%);
    border-radius: 16px;
}